
.page-title{
    /* font-weight: 100; */
    font-size: calc(2rem + 2vw);
}

.years, .months, .days, .hours, .minutes, .seconds{
    margin-right: calc(1rem + 1vw);
}

.timer-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.days-container, .hours-container, .minutes-container, .seconds-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-right: calc(1rem + 2vw);
}

.days-num, .hours-num, .minutes-num, .seconds-num {
   font-size: calc(2rem + 3vw);
}

.days-text, .hours-text, .minutes-text, .seconds-text, .roughly-text {
    font-size: calc(1rem + 1vw);
    font-weight: 100;
    color: gray;

}

.roughly-text{
    color: white;
}
